// =forms
//============================================================================*/

textarea {resize: none}

.no-arrow {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
  background: none;
  position: relative;
  z-index: 10;
}

.form-control{
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;

  @include no-box-shadow();
  @include border-radius($input_border_radius);
  border: none;
  border-bottom: $input_border;
  @include placeholder-color($input_placeholder_color);
  @include font-size($input_font_size);
  font-weight: $input_font_weight;
  color: $input_color;
  background: $input_bg;
  padding: $input_padding;
  height: auto;

  &:focus, &:active{
    @include no-box-shadow();
    border-color: $input_border_focus_color;
    background: $input_bg;
    color: $input_color;
  }
}

.select{
  position: relative;
  background: $input_bg;
  @include border-radius($input_border_radius);
  border: $input_border;

  select{
    border: none;
    padding-right: 25px;
    margin: 0px;
    background: none;
    @include no-box-shadow();

    &:focus{
      @include no-box-shadow();
    }
  }

  i{
    position: absolute;
    top: 10px;
    right: 10px;
    //z-index: 99;
    cursor: pointer;
  }
}

.select2-dropdown{
  border: $input_border;
}
.select2-container .select2-selection--single{
  @include no-box-shadow();
  @include border-radius($input_border_radius);
  border: none;
  border-bottom: $input_border;
  //@include placeholder-color($input_placeholder_color);
  @include font-size($input_font_size);
  line-height: 1.5;
  font-weight: $input_font_weight;
  color: $input_color;
  background: $input_bg;
  padding: $input_padding;
  height: auto;
}

.select2-container .select2-selection--single .select2-selection__rendered{
  padding: 0px;
  line-height: 1.5;
  color: $input_color;
}
.select2-container .select2-selection--single .select2-selection__placeholder{
  color: $input_placeholder_color;
}
.select2-container .select2-selection--single .select2-selection__arrow{
  top: 0px;
  bottom: 0px;
  height: 100%;
  color:$input_placeholder_color;

  position: absolute;
  right: 0px;
  width: 20px;
}
.select2-container .select2-selection--single .select2-selection__arrow b{
  border-color: $input_placeholder_color transparent transparent;
  border-style: solid;
  border-width: 5px 4px 0;
  height: 0;
  left: 50%;
  margin-left: -4px;
  margin-top: -2px;
  position: absolute;
  top: 50%;
  width: 0;
}

.select2-container.select2-container--open .select2-selection--single .select2-selection__arrow b {
  border-color: transparent transparent $input_placeholder_color;
  border-width: 0 4px 5px;
}

.checkbox{
  @include font-size($input_font_size - 2);
}
.checkbox > [type="checkbox"] {
  &:checked,
  &:not(:checked) {
    opacity: 0;
    position: absolute;
    & + label {
      display: inline-block;
      padding-left: 35px;
      position: relative;
      cursor: pointer;
      &:before {
        position: absolute;
        display: inline-block;
        top: 3px;
        left: 0;
        width: 15px;
        height: 15px;
        border: 1px solid $input_border_color;
        color: $input_border_color;
        @include border-radius($input_border_radius);
        background-color: $input_bg;
      }
      &:hover:before {
        background-color: $input_bg;
      }
    }
    &:focus + label:before {
      border-color: $input_border_color;
    }
    &[disabled] + label {
      cursor: not-allowed;
      opacity: .4;
      &:before {
        opacity: .7;
      }
    }
  }
}
.checkbox > [type="checkbox"]:checked + label:before,
.checkbox > [type="checkbox"]:not(:checked) + label:before {
  font-family: "Font Awesome 5 Free";
  @include font-size(15);
  text-align: center;
}
.checkbox > [type="checkbox"]:checked + label:before {
  content: "\f00c";
  color: $input_border_color;
  background-color: $input_bg;
  border-color: $input_border_color;
  line-height: 1;
}
.checkbox > [type="checkbox"]:not(:checked) + label:before {
  content: "";
}