  url('../fonts/basiersquare-semibold-webfont.woff2') format('woff2'),
  url('../fonts/basiersquare-semibold-webfont.woff') format('woff'),
  url('../fonts/basiersquare-semibold-webfont.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;

}

@font-face {
  font-family: 'gangster_groteskregular';
  src: url('../fonts/gangstergrotesk-regular-webfont.eot');
  src: url('../fonts/gangstergrotesk-regular-webfont.eot?#iefix') format('embedded-opentype'),
  url('../fonts/gangstergrotesk-regular-webfont.woff2') format('woff2'),
  url('../fonts/gangstergrotesk-regular-webfont.woff') format('woff'),
  url('../fonts/gangstergrotesk-regular-webfont.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;

}

