// -- helpers
$returnToTopBg: #000000;
$returnToTopColor: #ffffff;

// -- elements

$head_class_color: $main_text_color;
$head_class_font_family: $headline_font_family;
$head_class_font_weight: $main_font_weight;
$head_class_font_size: 2 * $main_font_size;
$head_class_line_height: $main_line_height;

$main_button_color: $main_text_color;
$main_button_color_hover: $main_button_color;
$main_button_font_weight: $main_font_weight;
$main_button_font_size: $main_font_size - 0.125rem;
$main_button_bg: $main_color;
$main_button_bg_hover: darken($main_button_bg, 5%);
$main_button_border_radius: 0px;

$archive_box_border: 1px solid #E8E8E8;
$archive_box_year_font_size: 25;

// -- blocks
$margin_block: 0 0 1.5rem 0;
$padding_col: $grid-gutter-width / 2;

$tab_bg: $main_button_bg;
$tab_color: $main_button_color;
$tab_active_bg: $main_button_bg_hover;
$tab_active_color: $main_button_color_hover;
$tab_active_border: $main_button_bg_hover;

$block_7_gutter: $grid-gutter-width;

$block_gallery_type_6_width: 9;
$block_gallery_type_6_height: 7;

// -- forms
$input_font_size: $main_font_size;
$input_font_weight: normal;
$input_color: $main_text_color;
$input_placeholder_color: lighten($input_color, 60%);
$input_bg: none;
$input_border_color: $main_border_color;
$input_border: 1px solid $input_border_color;
$input_padding: 0.6rem 0rem;
$input_border_focus_color: $input_border_color;
$input_border_radius: 0px;