/*
* MAIN MODULE STYLES
*/

@import "../../sass/parts/variables-main";
@import "../../sass/parts/custom";
@import "../../../node_modules/bootstrap/scss/mixins";
@import "../../sass/parts/mixins";
@import "variables";

.homepage-section{
  min-height: 100vh;
  @include display-flex();
  align-items: center;

  .arrow {
    margin: 0px;
    padding: 0px;
    background: transparent;
  }
}
.homepage-section__top{
  position: relative;
  z-index: 2;
}
.homepage-section__header{
  //@include translate(0%, 100%);
}

.homepage-section__buttons{
  padding-top: 2rem;
}

.homepage-section__baners-arrows{
  z-index: 2;
}

.homepage-section__baners{
  position: relative;
  z-index: 1;

  .hide{
    opacity: 0;
  }

  .arrow{
    position: absolute;
    top: 50%;
    @include translate(0%, -50%);
    margin: 0px;
    padding: 0px;
    background: transparent;
    z-index: 1;

    &.arrow-left{
      left: -$grid-gutter-width;
      @include translate(-100%, -50%);
    }
    &.arrow-right{
      right: -$grid-gutter-width;
      @include translate(100%, -50%);
    }

    &.slick-disabled{
      opacity: 0.1;
    }
  }
}

.homepage-section__baners-item{
  position: relative;
  width: 100%;
  overflow: hidden;

  &:before{
    content: "";
    display: block;
    padding-top: (1080/1920)*100%;//(466/735)*100%;
  }

  figure{
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;

    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    backface-visibility: hidden;
    //-webkit-transform: translate3d(0, 0, 0);
    //-moz-transform: translate3d(0, 0, 0);
    //transform: translate3d(0, 0, 0);

    img{
    }
  }
}

@include media-breakpoint-up(xxl){
  .homepage-section__header, .homepage-section__baners-arrows{
    position: absolute;
    //@include translate(0%, 100%);
    .arrow {
      padding: 0px 10px;
    }
  }
}
@include media-breakpoint-only(xl) {
  .homepage-section__header, .homepage-section__baners-arrows{
    position: absolute;
    //@include translate(0%, 100%);
    .arrow {
      padding: 0px 10px;
    }
  }
}
@include media-breakpoint-only(lg) {
  .homepage-section__header, .homepage-section__baners-arrows{
    position: absolute;
    //@include translate(0%, 100%);
    .arrow {
      padding: 0px 10px;
    }
  }
}
@include media-breakpoint-only(md) {
  .homepage-section__header{
    margin-bottom: 3.75rem;
    padding-right: 100px;
  }
  .homepage-section__baners-arrows{
    @include display-flex();
    justify-content: flex-end;
    align-items: center;
    position: absolute;
    left: 0px;
    right: 0px;
    top: -3.75rem;
    padding-left: $grid-gutter-width / 2 - 10px;
    padding-right: $grid-gutter-width / 2 - 10px;

    .arrow{
      margin-left: 10px;
      margin-right: 10px;
      @include translate(0%, -95%);
    }
  }
  .homepage-section__baners-item{
    margin-bottom: 3.75rem;
  }
}
@include media-breakpoint-only(sm) {
  .homepage-section__header{
    margin-bottom: 3.5rem;
    padding-right: 100px;
  }
  .homepage-section__baners-arrows{
    @include display-flex();
    justify-content: flex-end;
    align-items: center;
    position: absolute;
    left: 0px;
    right: 0px;
    top: -3.5rem;
    padding-left: $grid-gutter-width / 2 - 10px;
    padding-right: $grid-gutter-width / 2 - 10px;

    .arrow{
      margin-left: 10px;
      margin-right: 10px;
      @include translate(0%, -95%);
    }
  }
  .homepage-section__baners-item{
    margin-bottom: 3.5rem;
  }
}
@include media-breakpoint-down(xs) {
  .homepage-section__header{
    margin-bottom: 3rem;
    padding-right: 100px;
  }
  .homepage-section__baners-arrows{
    @include display-flex();
    justify-content: flex-end;
    align-items: center;
    position: absolute;
    left: 0px;
    right: 0px;
    top: -3rem;
    padding-left: $grid-gutter-width / 2 - 10px;
    padding-right: $grid-gutter-width / 2 - 10px;

    .arrow{
      margin-left: 10px;
      margin-right: 10px;
      @include translate(0%, -95%);
    }
  }
  .homepage-section__baners-item{
    margin-bottom: 3rem;
  }
}
