

@mixin head_block() {
  color: $head_class_color;
  font-family: $headline_font_family;
  @include font-size(1.375rem);
  font-weight: 400;
  line-height: 1.2;
  margin: 3rem 0 0 0;

  @include media-breakpoint-only(md) {
    margin: 2.5rem 0 0 0;
  }
  @include media-breakpoint-only(sm) {
    margin: 1.5rem 0 0 0;
  }
  @include media-breakpoint-down(xs) {
    margin: 1.5rem 0 0.5rem 0;
  }
}

// =block
//============================================================================*/
.block {
  margin: $margin_block;
  position: relative;
  clear: both;

  p{
    margin-bottom: 1em * $main_line_height;

    &:last-child{
      margin-bottom: 0;
    }
  }

  + .block-type-2{
    padding-top: 3rem;

    @include media-breakpoint-only(md) {
      padding-top: 2.5rem;
    }
    @include media-breakpoint-only(sm) {
      padding-top: 1.5rem;
    }
    @include media-breakpoint-down(xs) {
      padding-top: 1.5rem;
    }
  }

  &.block--no-space{
    margin-bottom: 0px !important;
  }

  figcaption {
    margin: 20px 0 0 0;
    text-align: center;
  }
  .block-float-left {
    float: left;
    padding: 0 (2 * $padding_col) $padding_col 0;
  }
  .block-float-right {
    float: right;
    padding: 0 0 $padding_col (2 * $padding_col);
  }
  .ico-file {
    margin: 0 15px 0 0;
    position: absolute;
    top: 0;
    left: $padding_col;
  }
  .link-file-mini {
    span {
      min-height: 30px;
      display: block;
      padding: 4px 0 0 40px;
    }
  }
  .link-file-medium {
    span {
      min-height: 50px;
      display: block;
      padding: 12px 0 0 70px;
    }
  }
  .link-file-big {
    span {
      min-height: 73px;
      display: block;
      padding: 23px 0 0 90px;
    }
  }
  .block-file-list{
    position: relative;
    @include display-flex();
    background: #f0f0f0;
    border-bottom: 1px solid $main_bg;
    overflow: hidden;
    flex-direction:row;
    flex-wrap:nowrap;

    &:hover, &:active, &:focus{
      background: $main_color;
      color: #fff;
    }

    > div{
      @include display-flex();
      align-items: center;
      justify-content: center;
      line-height: 1.2;
      padding: 10px 20px;

      border-right: 1px solid $main_bg;

      &:last-child{
        border-right: none;
      }

      > * {
        word-wrap: break-word;
        overflow-wrap: break-word;
        min-width: 1%;
      }
    }

    .block-file-list_name{
      @include flex(1,1);
      display: block;
      justify-content: flex-start;
      word-wrap: break-word;
      overflow-wrap: break-word;
      min-width: 1%;
    }
    .block-file-list_download{
      @include flex(0,0);
      width: 55px;
      text-align: center;
    }
    .block-file-list_format{
      white-space: nowrap;
      @include flex(0,0);
      width: 150px;

      span{
        text-transform: uppercase;
        display: inline-block;
        width: 40px;
        text-align: center;
      }
    }
    .block-file-list_size{
      white-space: nowrap;
      @include flex(0,0);
      width: 100px;
      text-align: right;
      justify-content: flex-end;
    }
  }
  .head-block {
    @include head_block();
  }
  .block-slide-content {
    display: none;
    margin: 20px 0 0 0;

    .block {
      &:last-child {
        margin: 0;
      }
    }
  }

  .block-slide-header {
    cursor: pointer;
    padding: 10px 15px;
    background: $tab_bg;
    color: $tab_color;

    i {
      float: right;
      margin: 5px 0 0 0;
    }
  }
  .block-slide-tab {
    margin: 0 0 1px;
  }
  .block-tabs-id {
    background: $tab_bg;
    color: $tab_color;

    .resp-arrow {
      border-top: 12px solid $tab_color;
    }
    &.resp-tab-active, .resp-tab-active {
      background: $tab_active_bg !important;
      color: $tab_active_color !important;
      border-top: 1px solid $tab_active_border !important;
      border-color: $tab_active_border !important;

      .resp-arrow {
        border-top: none;
        border-bottom: 12px solid $tab_active_color;
      }
    }
  }
  .resp-tab-content {
    border-color: $tab_active_border;

    .block {
      &:last-child {
        margin: 0;
      }
    }
  }
  &.block-multiple-column-1 {
    @include multiple-column(1);
  }
  &.block-multiple-column-2 {
    @include multiple-column(2);
  }
  &.block-multiple-column-3 {
    @include multiple-column(3);
  }

  &.block-type-7{
    figure{
      margin:0px;
    }
    .block-gallery-slider, .block-gallery-thumbs{
      display: none;

      &.slick-initialized{
        display: block;
      }
    }
    &.block-gallery-type-1{
      .block-gallery-slider{
        padding-bottom: $block_7_gutter / 2;
      }
      .block-gallery-thumbs{
        margin-left: -$block_7_gutter / 2;
        margin-right: -$block_7_gutter / 2;

        .item{
          padding: $block_7_gutter / 2;
          cursor: pointer;
        }
      }
    }

    &.block-gallery-type-2{
      .block-gallery-slider{
        margin-left: -$block_7_gutter / 2;
        margin-right: -$block_7_gutter / 2;

        .item{
          padding: $block_7_gutter / 2;
          cursor: pointer;
        }
      }
    }

    &.block-gallery-type-3{}

    &.block-gallery-type-4{
      .block-photo{
        margin-bottom: $block_7_gutter;
      }
    }

    &.block-gallery-type-5{
      .block-photo{
        margin-bottom: $block_7_gutter;
      }
    }

    &.block-gallery-type-6{

      .block-photo{
        height: auto;

        figure{
          position: absolute;
          top: 0px;
          left: 0px;
          right: 0px;
          bottom: 0px;

          @include transition();

          -webkit-backface-visibility: hidden;
          -moz-backface-visibility: hidden;
          backface-visibility: hidden;

          background-size: cover;

          img{
            width: 100%;
            height: 100%;
            object-fit: cover;

            position: static;
            transform: translate(0%, 0%);
            -moz-transform: translate(0%, 0%);
            -webkit-transform: translate(0%, 0%);
          }
        }
      }

      /*            .block-gallery-row{
                      opacity: 0.5;
                  }*/

      .block-gallery-row-1{
        .row-sizer{
          width: 5%;
        }
        .row-item{
          float: left;
        }
        .row-item-1{
          width: 30%;

          .block-photo{
            &:before{
              //padding-top: 80%;
              padding-top: (33/30)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
        .row-item-2{
          width: 30%;

          .block-photo{
            &:before{
              padding-top: (33/30)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
        .row-item-3{
          width: 40%;

          .block-photo{
            &:before{
              padding-top: (50/40)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
        .row-item-4{
          width: 60%;

          .block-photo{
            &:before{
              padding-top: (67/60)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
        .row-item-5{
          width: 40%;

          .block-photo{
            &:before{
              padding-top: (50/40)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
      }

      .block-gallery-row-2{
        .row-sizer{
          width: 5%;
        }
        .row-item{
          float: left;
        }
        .row-item-5{
          width: 30%;

          .block-photo{
            &:before{
              //padding-top: 80%;
              padding-top: (33/30)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
        .row-item-4{
          width: 30%;

          .block-photo{
            &:before{
              padding-top: (33/30)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
        .row-item-3{
          width: 40%;

          .block-photo{
            &:before{
              padding-top: (50/40)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
        .row-item-2{
          width: 60%;

          .block-photo{
            &:before{
              padding-top: (67/60)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
        .row-item-1{
          width: 40%;

          .block-photo{
            &:before{
              padding-top: (50/40)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
      }

      .block-gallery-row-3{
        .row-sizer{
          width: 2%;
        }
        .row-item{
          float: left;
        }
        .row-item-1{
          width: 36%;

          .block-photo{
            &:before{
              //padding-top: 80%;
              padding-top: (50/36)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
        .row-item-2{
          width: 32%;

          .block-photo{
            &:before{
              padding-top: (60/32)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
        .row-item-3{
          width: 32%;

          .block-photo{
            &:before{
              padding-top: (60/32)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
        .row-item-4{
          width: 36%;

          .block-photo{
            &:before{
              padding-top: (50/36)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
        .row-item-5{
          width: 2 * 32%;

          .block-photo{
            &:before{
              padding-top: (40/(2* 32))*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
      }

      .block-gallery-row-4{
        .row-sizer{
          width: 2%;
        }
        .row-item{
          float: left;
        }
        .row-item-3{
          width: 36%;

          .block-photo{
            &:before{
              //padding-top: 80%;
              padding-top: (65/36)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
        .row-item-1{
          width: 32%;

          .block-photo{
            &:before{
              padding-top: (40/32)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
        .row-item-2{
          width: 32%;

          .block-photo{
            &:before{
              padding-top: (40/32)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
