      .block-gallery-row-5{
        //opacity: 1;

        .row-sizer{
          width: 1%;
        }
        .row-item{
          float: left;
        }
        .row-item-1{
          width: 42%;

          .block-photo{
            &:before{
              //padding-top: 80%;
              padding-top: (50/42)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
        .row-item-2{
          width: 58%;

          .block-photo{
            &:before{
