            }
          }
        }
        .row-item-3{
          width: 42%;

          .block-photo{
            &:before{
              padding-top: (50/42)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
        .row-item-4{
          width: 29%;

          .block-photo{
            &:before{
              padding-top: (30/29)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
        .row-item-5{
          width: 29%;

          .block-photo{
            &:before{
              padding-top: (30/29)*($block_gallery_type_6_height*100%/$block_gallery_type_6_width);
            }
          }
        }
      }
    }

    &.block-gallery-type-7{
      .block-photo{
        margin-bottom: $block_7_gutter;
      }
    }

    .arrow{
      position: absolute;
      top: 50%;
      @include translate(0%, -50%);
      font-size: 20px;
      margin: 0px;
      padding: 5px 10px;
      z-index: 5;
      border: none;

      @include transition();

      &.arrow-left{
        left: $block_7_gutter;
      }

      &.arrow-right{
        right: $block_7_gutter;
      }

      &:hover, &:active, &:focus {
        &:not(.slick-disabled){
          background: #fff;
        }
      }

      &.slick-disabled{
        opacity: 0.5;
      }
    }
  }
}

.to-center {
  img {
    margin: 0 auto;
  }
}

@media (max-width: 767px) {
  // =blocks
  //========================================================================*/
  .block {
    .block-float-left {
      float: none;
      padding: 0 0 $padding_col 0;
    }
    .block-float-right {
      float: none;
      padding: 0 0 $padding_col 0;
    }
    .link-file-mini, .link-file-medium, .link-file-big {
      span {
        margin: 0 0 15px 0;
      }
    }
    &.block-type-4 {
      img {
        margin: 15px auto 0 auto;
      }
    }
    img {
      margin: 0 auto 15px auto;
    }

    .block-file-list{
      > div{
        padding: 10px 10px;
      }

      //.block-file-list_name{
      //    width: 100%;
      //    justify-content: flex-start;
      //}
      .block-file-list_download{
        width: 45px;
      }
      .block-file-list_format{
        display: none;
      }
      .block-file-list_size{
        width: 90px;
      }
    }

    &.block-multiple-column-1 {
      @include multiple-column();
    }
    &.block-multiple-column-2 {
      @include multiple-column();
    }
    &.block-multiple-column-3 {
      @include multiple-column();
    }
  }

  // =news
  //========================================================================*/
  .news-block {

    .news-block-photo {
      margin: 0 0 20px 0;
    }
  }
}
