/*
* MAIN MODULE STYLES
*/

@import "../../sass/parts/variables-main";
@import "../../sass/parts/custom";
@import "../../../node_modules/bootstrap/scss/mixins";
@import "../../sass/parts/mixins";
@import "variables";

.menu-module{
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  min-height: 100vh;
  background: rgba(#fff, 1);
  z-index: 200;
  display: none;
  opacity: 0;
  overflow: auto;
}

.menu-module--open{
  display: block;
}

.menu-module__header{
  position: fixed;
  top: 0rem;
  right: 0rem;
  left: 0rem;
  text-align: center;
  @include transition(2s, padding, cubic-bezier(.165,.84,.44,1));
  min-height: 5rem;
  padding: 0rem;
  z-index: 100;
  background: rgba($main_bg,0.5);

  @include display-flex();
  align-items: center;
  justify-content: center;

  #menu-logo-path{
    fill: $main_text_color;
  }
}

.menu-module__content{
  min-height: 100vh;
  @include display-flex();
  justify-content: center;
  align-items: center;
  //line-height: 1.2;
}

.menu-module__block, .menu-module__lang{
  ul{
    list-style: none;
    margin: 0px;
    padding: 0px;

    li{
      margin-bottom: 0.7rem;
    }
  }
}

.menu-module__headline{
  @include font-size(1.375rem);
  font-family: $headline_font_family;
  padding-bottom: 2rem;
}

.menu-module__lang{
  align-items: flex-end;

  ul{
    margin-left: -8px;
    margin-right: -8px;

    li{
      padding-left: 8px;
      padding-right: 8px;
      display: inline-block;
    }
  }
}

.menu-module__footer{
  position: absolute;
  left: 0px;
  right: 0px;
  bottom: 0px;
  text-align: center;

  ul{
    margin-left: 0px;
    margin-right: 0px;
    margin-bottom: 100px;

    li{
      padding-left: 15px;
      padding-right: 15px;
      @include font-size(1.375rem);
    }
  }
}

@include media-breakpoint-up(xxl){
}
@include media-breakpoint-only(xl) {
}
@include media-breakpoint-only(lg) {
}
@include media-breakpoint-only(md) {
  .menu-module__content{
    text-align: center;
    padding-bottom: 120px;
  }
  .menu-module__block{
    display: none;
  }
}
@include media-breakpoint-only(sm) {
  .menu-module__header {
    min-height: 4.5rem;
  }
  .menu-module__content{
    text-align: center;
    padding-bottom: 100px;
  }
  .menu-module__block{
    display: none;
  }
  .menu-module__footer{
    ul{
      margin-bottom: 90px;
    }
  }
}
@include media-breakpoint-down(xs) {
  .menu-module__header {
    min-height: 4rem;
  }
  .menu-module__content{
    text-align: center;
    padding-bottom: 100px;
  }
  .menu-module__block{
    display: none;
  }
  .menu-module__headline{
    padding-bottom: 1.5rem;
  }
  .menu-module__footer{
    ul{
      margin-bottom: 80px;
    }
  }
}
