// =helpers
//============================================================================*/
.back-link {
  display: inline-block;
  margin: 0 0 20px 0;
}
.link-absolute {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

// =return-to-top
//============================================================================*/


#return-to-top {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 20px;
  background: fade($returnToTopBg, 70%);
  width: 50px;
  height: 50px;
  text-decoration: none;
  @include border-radius(35px);
  @include transition(0.3s);

  i {
    color: $returnToTopColor;
    margin: 0;
    position: relative;
    left: 16px;
    top: 13px;
    @include font-size(19);
    @include transition(0.3s);
  }
  &:hover {
    background: fade($returnToTopBg, 90%);

    i {
      color: $returnToTopColor;
      top: 5px;
    }
  }
}

// =info_cookie
//============================================================================*/


#info_cookie {
  @include font-size(0.9rem);
  padding: 2rem 0rem;

  @include media-breakpoint-down(xs){
.infocookie-close{
  display: inline-block;
  margin-top: 1rem;
}
  }
}

// =photo
//============================================================================*/
.block-photo, .news-block-photo{
  position: relative;
  width: 100%;
  overflow: hidden;
  text-align: center;
  @include transition();

  &:before{
    content: "";
    display: block;
    @include transition();
  }

  &.crop-photo-2, &.crop-photo-3, &.crop-photo-4, &.crop-photo-5, &.crop-photo-6{
    height: auto;

    figure{
      position: absolute;
      top: 0px;
      left: 0px;
      right: 0px;
      bottom: 0px;

      @include transition();

      -webkit-backface-visibility: hidden;
      -moz-backface-visibility: hidden;
      backface-visibility: hidden;

      background-size: cover;

      img{
        width: 100%;
        height: 100%;
        object-fit: cover;

        position: static;
        transform: translate(0%, 0%);
        -moz-transform: translate(0%, 0%);
        -webkit-transform: translate(0%, 0%);
      }
    }
  }
  &.crop-photo-2{
    &:before{
      padding-top: 100%;
    }
  }

  &.crop-photo-3{
    &:before{
      padding-top: (3/4)*100%;
    }
  }

  &.crop-photo-4{
    &:before{
      padding-top: (9/16)*100%;
    }
  }

  &.crop-photo-5{
    &:before{
      padding-top: (4/3)*100%;
    }
  }

  &.crop-photo-6{
    &:before{
      padding-top: (16/9)*100%;
    }
  }

  &.crop-position-photo-1{
    figure{
      background-position: 0% 0%;
      img{
        object-position: 0% 0%;
      }
    }
  }
  &.crop-position-photo-2{
    figure{
      background-position: 50% 0%;
      img{
        object-position: 50% 0%;
      }
    }
  }
  &.crop-position-photo-3{
    figure{
      background-position: 100% 0%;
      img{
        object-position: 100% 0%;
      }
    }
  }
  &.crop-position-photo-4{
    figure{
      background-position: 0% 50%;
      img{
        object-position: 0% 50%;
      }
    }
  }
  &.crop-position-photo-5{
    figure{
      background-position: 50% 50%;
      img{
        object-position: 50% 50%;
      }
    }
  }
  &.crop-position-photo-6{
    figure{
      background-position: 100% 50%;
      img{
        object-position: 100% 50%;
      }
    }
  }
  &.crop-position-photo-7{
    figure{
      background-position: 0% 100%;
      img{
        object-position: 0% 100%;
      }
    }
  }
  &.crop-position-photo-8{
    figure{
      background-position: 50% 100%;
      img{
        object-position: 50% 100%;
      }
    }
  }
  &.crop-position-photo-9{
    figure{
      background-position: 100% 100%;
      img{
        object-position: 100% 100%;
      }
    }
  }
}

// =no-padding
//============================================================================*/

.no-padding{
  padding-left: 0px !important;
  padding-right: 0px !important;
}

// =dropdown-backdrop
//============================================================================*/

.dropdown-backdrop {
  position: static;
}

// =fit-box
//============================================================================*/

.fit-box{
  img{
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

// =fill-box
//============================================================================*/

.fill-box{
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

// =img-responsive
//============================================================================*/

.img-responsive{
  @include img-fluid;
}

#rotate-screen{
  display: none;
  position: fixed;
  top: 0rem;
  left: 0rem;
  right: 0rem;
  bottom: 0rem;
  align-items: center;
  justify-content: center;
  z-index: 200;
  color: map_get($main_colors, 'color-3');
  background: map_get($main_bgs, 'color-4');

  .head-class{
    color: map_get($main_colors, 'color-3');
    margin: 0rem;
  }
}

@media only screen and (max-width: map-get($grid-breakpoints, md))  and (orientation:landscape){
  #rotate-screen{
    @include display-flex();
  }
}