.menu-button {
  position: absolute;
  top: 50%;
  right: 28px;
  -webkit-transform: translate(0%, -50%);
  -moz-transform: translate(0%, -50%);
  -ms-transform: translate(0%, -50%);
  -o-transform: translate(0%, -50%);
  transform: translate(0%, -50%);
  transition: opacity 2s cubic-bezier(0.165, 0.84, 0.44, 1);
  -webkit-transition: opacity 2s cubic-bezier(0.165, 0.84, 0.44, 1);
  -moz-transition: opacity 2s cubic-bezier(0.165, 0.84, 0.44, 1);
  -o-transition: opacity 2s cubic-bezier(0.165, 0.84, 0.44, 1);
  opacity: 1;
  width: 40px;
  height: 40px;
  margin: 1px 0px 0px;
  padding: 0px;
  cursor: pointer;
  background: none;
  z-index: 300; }
  .menu-button * {
    transition: width 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
    -webkit-transition: width 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
    -moz-transition: width 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
    -o-transition: width 0.6s cubic-bezier(0.165, 0.84, 0.44, 1); }
  .menu-button span {
    display: block;
    background: #13333A; }
  .menu-button .hamburger {
    position: absolute;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%); }
    .menu-button .hamburger span {
      width: 32px;
      height: 1px;
      position: absolute;
      top: 3px;
      right: 4px;
      margin: 10px 0; }
      .menu-button .hamburger span:nth-child(1) {
        transition-delay: .5s; }
      .menu-button .hamburger span:nth-child(2) {
        transition-delay: .625s;
        top: 17px; }
  .menu-button .cross {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0px;
    left: 0px;
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg); }
    .menu-button .cross span:nth-child(1) {
      height: 0%;
      width: 1px;
      position: absolute;
      top: 10%;
      left: 19px;
      transition-delay: 0s;
      transition-property: height; }
    .menu-button .cross span:nth-child(2) {
      width: 0%;
      height: 1px;
      position: absolute;
      left: 10%;
      top: 19px;
      transition-delay: .25s; }
  .menu-button.open .hamburger span {
    width: 0%; }
    .menu-button.open .hamburger span:nth-child(1) {
      transition-delay: 0s; }
    .menu-button.open .hamburger span:nth-child(2) {
      transition-delay: .125s; }
    .menu-button.open .hamburger span:nth-child(3) {
      transition-delay: .25s; }
  .menu-button.open .cross span:nth-child(1) {
    height: 80%;
    transition-delay: .625s; }
  .menu-button.open .cross span:nth-child(2) {
    width: 80%;
    transition-delay: .375s; }

