/*
* MAIN MODULE STYLES
*/

@import "../../sass/parts/variables-main";
@import "../../sass/parts/custom";
@import "../../../node_modules/bootstrap/scss/mixins";
@import "../../sass/parts/mixins";
@import "variables";


.intro-section{
  min-height: inherit;
  text-align: center;
  border-bottom: $main_border;
  overflow: hidden;

  .container{
    min-height: inherit;

    .row{
      min-height: inherit;
    }
  }
}
.intro-dsc{
  margin: 2rem 0rem;

  .button{
    margin-top: 1rem;
  }
}
.intro-video{
}
.intro-logo{
  padding: 4.125rem 0rem;
  img,svg{
    height: 5.0625rem;
  }

  #logo-path{
    fill: $main_text_color;
  }

  &.fixed{
    img,svg{
      opacity: 0;
    }
  }

  a{
    &:after{
      content: none;
    }
  }
}

/*
* RESPONSIVE
*/

@include media-breakpoint-up(xxl){
  .intro-video{
    padding: 0px 3.4375rem;
  }
}
@include media-breakpoint-only(xl) {
  .intro-video{
    padding: 0px 2rem;
  }
}
@include media-breakpoint-only(lg) {
  .intro-logo {
    img, svg {
      height: 4.25rem;
    }
  }
}
@include media-breakpoint-only(md) {
  .intro-section{
    //padding: 5rem 0rem;
  }
  .intro-video{
    margin: 5rem 0rem;
  }
  .intro-logo {
    img, svg {
      height: 4.25rem;
    }
  }
}
@include media-breakpoint-only(sm) {
  .intro-section{
    //padding: 4rem 0rem;
  }
  .intro-video{
    margin: 4rem 0rem;
  }
  .intro-logo {
    img, svg {
      height: 3.5rem;
    }
  }
}
@include media-breakpoint-down(xs) {
  .intro-section{
    //padding: 0rem;
  }
  .intro-dsc {
    margin: 2rem 0rem 1rem;
  }
  .intro-video{
    margin: 0rem;
  }
  .intro-logo {
    padding: 2rem 0rem 3rem;

    img, svg {
      height: 2rem;
    }
  }
}
