@import "variables-main";

// =main
//============================================================================*/
html{
  font-size: 16px;
  min-height: 100vh;
}
body {
  font-weight: $main_font_weight;
  font-family: $main_font_family;
  @include font-size($main_font_size);
  line-height: $main_line_height;
  font-weight: $main_font_weight;
  color: $main_text_color;
  background: $main_bg;
  text-decoration: none;
  padding: 0;
  margin: 0 auto;
  min-height: 100vh;
}
* {
  outline: none;
}
a {
  color: $link_color;
  text-decoration: $link_text_decoration;
  @include transition();
  outline: none;

  &:hover, &:focus, &:active {
    color: $link_hover_color;
    text-decoration: $link_text_decoration;
    outline: none;

    &:after{
      right: 0%;
    }
  }

  position: relative;
  background: transparent;

  &:after{
    content: "";
    display: block;
    left: 0;
    right: 100%;
    height: 1px;
    bottom: -3px;
    position: absolute;
    background: $link_color;
    @include transition(.6s, all, cubic-bezier(.165,.84,.44,1));
  }
}
button {
  border: none;
  outline: none;

  &:focus {
    outline: none;
  }
}
strong,b{
  font-family: 'basier_squaresemibold';
  font-weight: normal;
}
figure{
  margin: 0;
}
label.error {
  color: $error_color;
  font-weight: bold;
  display: none;
}

#swup{
  min-height: 100vh;
}

.table{
  td{
    &:first-child{
      padding-left: 0;
    }
    &:last-child{
      padding-right: 0;
    }
  }
}

// =colorset
//============================================================================*/

@each $name, $color in $main_colors {
  .colorset--#{$name} {
    color: $color;
    background: map_get($main_bgs, $name);

    &.colorset--transparent{
      background: rgba(map_get($main_bgs, $name), 0.5);
    }

    .head-class {
      color: $color;

      a{
        color: $color;
      }
    }

    .block .head-block {
      color: $color;
    }

    .button, a {
      color: $color;

      &:after {
        background: $color;
      }

      &:active, &:hover, &:focus {
        color: $color;
      }
    }

    .arrow {
      .arrow-path {
        stroke: $color;
      }
    }

    .form-control {
      //border: none;
      border-bottom: 1px solid rgba($color, 0.8);
      @include placeholder-color(rgba($color, 0.5));
      color: $color;
      //background: $input_bg;

      &:focus, &:active {
        color: $color;
        //border-color: $input_border_focus_color;
        //background: $input_bg;
      }
    }

    .select {
      //background: $input_bg;
      //border: $input_border;
    }

    .checkbox > [type="checkbox"] {
      &:checked,
      &:not(:checked) {
        & + label {
          &:before {
            //border: 1px solid $input_border_color;
            //color: $input_border_color;
            //background-color: $input_bg;
          }

          &:hover:before {
            //background-color: $input_bg;
          }
        }

        &:focus + label:before {
          //border-color: $input_border_color;
        }
      }
    }

    //.checkbox > [type="checkbox"]:checked + label:before {
    //  color: $input_border_color;
    //  background-color: $input_bg;
    //  border-color: $input_border_color;
    //}

    .table{
      color: $color;
      border-color: rgba($color,0.2);
      border-bottom: solid 1px rgba($color,0.2);

      tr, td{
        border-color: rgba($color,0.2);
      }
    }
  }

  .select2-container--colorset--#{$name} .select2-dropdown {
    border: 1px solid rgba($color, 0.8);
    background: $color;
  }

  .select2-container--colorset--#{$name} .select2-selection--single {
    //border: none;
    border-bottom: 1px solid rgba($color, 0.8);
    color: $color;
    //background: $input_bg;
  }

  .select2-container--colorset--#{$name} .select2-selection--single .select2-selection__rendered {
    color: $color;
  }
  .select2-container--colorset--#{$name} .select2-selection--single .select2-selection__placeholder {
    color: rgba($color, 0.5);
  }
  .select2-container--colorset--#{$name} .select2-selection--single .select2-selection__arrow {
    color: rgba($color, 0.5);
  }
  .select2-container--colorset--#{$name} .select2-selection--single .select2-selection__arrow b {
    border-color: rgba($color, 0.5) transparent transparent;
  }

  .select2-container--colorset--#{$name}.select2-container--open .select2-selection--single .select2-selection__arrow b {
    border-color: transparent transparent rgba($color, 0.5);
  }

  .select2-container--colorset--#{$name} .select2-results__option[aria-selected=true]{
    background: rgba($color, 0.5);
  }

  .select2-container--colorset--#{$name} .select2-results__option--highlighted[aria-selected] {
    color: $color;
    background: map_get($main_bgs, $name);
  }
}

.colorset--color-4 {
  &.colorset--transparent {
    background: rgba(map_get($main_bgs, color-4), 0.9);
  }
}

// =waypoint
//============================================================================*/

[data-waypoint-unanimate]{
  @include transition(2s, all, cubic-bezier(.165,.84,.44,1));

  &.animate{
    opacity: 0;
    @include translate(0%,-3rem);
  }
}

[data-waypoint-animate]{
  @include transition(2s, all, cubic-bezier(.165,.84,.44,1));
  @include translate(0%,3rem);
  opacity: 0;

  &.animate{
    opacity: 1;
    @include translate(0%,0%);
  }
}

.transition-fade {
  @include transition(1.2s, opacity, cubic-bezier(.165,.84,.44,1));
  opacity: 1;
}

html.is-animating .transition-fade {
  opacity: 0;
}


// =section
//============================================================================*/

.section{
  padding: 150px 0px 100px;
}

// =content
//============================================================================*/

.content-header{
  min-height: 495px + 80px;
  padding: 150px 0px 70px;

  @include display-flex();
  justify-content: center;
  align-items: center;

  text-align: center;
}

.content-header__category{
  margin-bottom: 1.3rem;
}
.content-header__title{
  .head-class{
    margin-bottom: 1.3rem;
  }
}

.content-header__dsc{
  padding-top: 1rem;
}

.content-list{}

.content-list__item{
  padding: 3.25rem 0rem 3.25rem + 2.5rem;
  min-height: 32.5rem;

  @include display-flex();
  justify-content: center;
  align-items: center;
}

.content-list__item-image{
  //text-align: center;

  img{
    display: block;
  }
}

.content-list__item-info{
  //padding-bottom: 2.5rem;
}

.content-list__item-title{
  //padding-bottom: 0.4rem;
}

.content-list__item-dsc{
  //padding-bottom: 1rem;
}

.content-article, .content-news-list{
  padding: 15rem 0rem 8rem;
}
.content-article__header{
  text-align: center;
  padding-bottom: 1rem;
}

.content-header {
  & + .content-article, & + .content-news-list{
    padding: 5.4375rem 0rem;

    &.colorset--color-1{
      background: #F4F4F4;
    }
  }
}

.content-article__navigation{
  padding: 3.5rem 0rem 1rem;
}

.content-news-list__item{
  padding: 0rem 0rem 3.75rem;
}

.content-news-list__item--first{
  text-align: center;

  .content-news-list__item-title{
    .head-class{
      @include head_class();
      margin-bottom: 0rem;
    }
  }

  .content-news-list__item-dsc{
    @include head_class();
    margin-bottom: 1.75rem;
  }

  .content-news-list__item-info{
    padding: 2.5rem 0 0rem;
  }
}

.content-news-list__item-image{
  margin-bottom: 1.75rem;

  img{
    width: 100%;
  }
}
.content-news-list__item-info{}
.content-news-list__item-category{
  margin-bottom: 1.75rem;
}
.content-news-list__item-title{
  margin-bottom: 1rem;
  @include font-size(1.375rem);
  line-height: normal;

  .head-class{
    margin-bottom: 0rem;
    @include font-size(1.375rem);
    line-height: normal;
  }
}
.content-news-list__item-dsc{
  margin-bottom: 1.75rem;
  @include font-size(1.375rem);
}
.content-news-list__item-date{
  margin-bottom: 1.75rem;
}

@include media-breakpoint-up(xxl){

}
@include media-breakpoint-only(xl) {

}
@include media-breakpoint-only(lg) {

}
@include media-breakpoint-only(md) {
  .content-article, .content-news-list{
    padding: 12rem 0rem 7rem;
  }
  .content-list__item-image{
    padding-bottom: 1.5rem;
  }

  .content-news-list__item--first{
    .content-news-list__item-info{
