    }
  }
}
@include media-breakpoint-only(sm) {
  html{
    font-size: 15px;
  }
  // =section
  //============================================================================*/

  .section{
    padding: 120px 0px 100px;
  }


  .content-article, .content-news-list{
    padding: 12rem 0rem 6rem;
  }
  .content-list__item{
    padding: 3rem 0rem 3rem + 2rem;
  }
  .content-list__item-image{
    padding-bottom: 2rem;
  }
  .content-list__item-info{
    //padding-bottom: 2rem;
  }

  .content-news-list__item--first{
    .content-news-list__item-info{
      padding: 1.5rem 0 0rem;
    }
  }
}
@include media-breakpoint-down(xs) {
  html{
    font-size: 18px;
  }

  .container{
    max-width: 100%;
    padding-left: 40px;
    padding-right: 40px;
  }

  // =section
  //============================================================================*/

  .section{
    padding: 100px 0px 80px;
  }

  .content-article, .content-news-list{
    padding: 9rem 0rem 6rem;
  }
  .content-list__item{
    padding: 3rem 0rem 3rem + 1.5rem;
  }
  .content-list__item-image{
    padding-bottom: 2.5rem;
  }
  .content-list__item-info{
    //padding-bottom: 1.5rem;
  }

  .content-news-list__item--first{
    text-align: left;

    .content-news-list__item-info{
      padding: 0rem;
    }
  }

  .content-news-list__item--first{
    .content-news-list__item-title{
      @include font-size($main_font_size);
      line-height: $main_line_height;

      .head-class{
        @include font-size($main_font_size);
        line-height: $main_line_height;
      }
    }

    .content-news-list__item-dsc {
      @include font-size($main_font_size);
      line-height: $main_line_height;
    }
  }

  .content-news-list__item-title{
    @include font-size($main_font_size);
    line-height: $main_line_height;

    .head-class{
      @include font-size($main_font_size);
      line-height: $main_line_height;
    }
  }
  .content-news-list__item-dsc{
    @include font-size($main_font_size);
    line-height: $main_line_height;
  }
}

@media (max-width: 480px) {
  .container {
    max-width: 100%;
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media (max-width: 380px) {
  .container {
    max-width: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }
}