// =mixins
//============================================================================*/
@mixin center-absolute($top: 50%, $left: 50%) {
  position: absolute;
  top: $top;
  left: $left;
  transform: translate(-$left, -$top);
  -moz-transform: translate(-$left, -$top);
  -webkit-transform: translate(-$left, -$top);
}

@mixin no-center-absolute() {
  position: relative;
  top: auto;
  left: auto;
  transform: translate(0px, 0px);
  -moz-transform: translate(0px, 0px);
  -webkit-transform: translate(0px, 0px);
}

@mixin border-radius($size: 50%) {
  -webkit-border-radius: $size;
  -moz-border-radius: $size;
  border-radius: $size;
}

@mixin transition($time: 0.5s, $item: All, $effect: ease) {
  transition: $item $time $effect;
  -webkit-transition: $item $time $effect;
  -moz-transition: $item $time $effect;
  -o-transition: $item $time $effect;
}

@mixin placeholder($font_weight: normal, $color: #d3d3d3, $font_size: 14) {
  *::-webkit-input-placeholder {
    color: $color !important;
    font-weight: $font_weight;
    @include font-size($font_size);
  }
  *:-moz-placeholder {
    color: $color !important;
    font-weight: $font_weight;
    @include font-size($font_size);
  }
  *::-moz-placeholder {
    color: $color !important;
    font-weight: $font_weight;
    @include font-size($font_size);
  }
  *:-ms-input-placeholder {
    color: $color !important;
    font-weight: $font_weight;
    @include font-size($font_size);
  }
}

@mixin box-shadow($inset: null, $distance_x: 0, $distance_y: 0, $blur: 20px, $spread: 0, $color: #000000) {
  -moz-box-shadow: $inset $distance_x $distance_y $blur $spread $color;
  -webkit-box-shadow: $inset $distance_x $distance_y $blur $spread $color;
  box-shadow: $inset $distance_x $distance_y $blur $spread $color;
}

@mixin no-box-shadow() {
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
  box-shadow: none;
}

@mixin multiple-column($col: auto) {
  -webkit-column-count: $col;
  -moz-column-count: $col;
  column-count: $col;
}

@mixin multiple-column-gap($gap: 30px) {
  -webkit-column-gap: $gap;
  -moz-column-gap: $gap;
  column-gap: $gap;
}

@mixin multiple-column-width($width: auto) {
  -webkit-column-width: $width;
  -moz-column-width: $width;
  column-width: $width;
}

@mixin multiple-column-rule($rule: $main_border) {
  -webkit-column-rule: $main_border;
  -moz-column-rule: $main_border;
  column-rule: $main_border;
}

@mixin multiple-column-break($break: avoid) {
  > *{
    -webkit-column-break-inside:$break;
    -moz-column-break-inside:$break;
    -o-column-break-inside:$break;
    -ms-column-break-inside:$break;
    column-break-inside:$break;
  }
}

@mixin suqre() {
  height: 0;
  padding-bottom: 100%;
}

@mixin display-flex($important: null) {
  display: -webkit-box $important;      /* OLD - iOS 6-, Safari 3.1-6 */
  display: -moz-box $important;         /* OLD - Firefox 19- (buggy but mostly works) */
  display: -ms-flexbox $important;      /* TWEENER - IE 10 */
  display: -webkit-flex $important;     /* NEW - Chrome */
  display: flex $important;             /* NEW, Spec - Opera 12.1, Firefox 20+ */
}

@mixin flex($grow: 1, $shrink: 1, $basis: auto, $width: auto){
  -webkit-box-flex: $grow $shrink $basis;      /* OLD - iOS 6-, Safari 3.1-6 */
  -moz-box-flex: $grow $shrink $basis;         /* OLD - Firefox 19- */
  width: $width;               /* For old syntax, otherwise collapses. */
  -webkit-flex: $grow $shrink $basis;          /* Chrome */
  -ms-flex: $grow $shrink $basis;              /* IE 10 */
  flex: $grow $shrink $basis;
}

@mixin order($order){
  -webkit-box-ordinal-group: $order;   /* OLD - iOS 6-, Safari 3.1-6 */
  -moz-box-ordinal-group: $order;      /* OLD - Firefox 19- */
  -ms-flex-order: $order;              /* TWEENER - IE 10 */
  -webkit-order: $order;               /* NEW - Chrome */
  order: $order;                       /* NEW, Spec - Opera 12.1, Firefox 20+ */
}

@mixin filter($filterString: 'grayscale(1)') {
  -webkit-filter: $filterString;
  -moz-filter: $filterString;
  -o-filter: $filterString;
  -ms-filter: $filterString;
  filter: $filterString;
}

@mixin hyphenate() {
  -ms-word-break: break-all;
  word-break: break-all;

  // Non standard for webkit
  word-break: break-word;

  -webkit-hyphens: auto;
  -moz-hyphens: auto;
  hyphens: auto;
}

@mixin placeholder-color($color) {
  &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color: $color;
  }
  &::-moz-placeholder { /* Firefox 19+ */
    color: $color;
  }
  &:-ms-input-placeholder { /* IE 10+ */
    color: $color;
  }
  &:-moz-placeholder { /* Firefox 18- */
    color: $color;
  }
}

@mixin gradient ($startColor: #eee, $endColor: white) {
  background-color: $startColor;
  background: -webkit-gradient(linear, left top, left bottom, from($startColor), to($endColor));
  background: -webkit-linear-gradient(top, $startColor, $endColor);
  background: -moz-linear-gradient(top, $startColor, $endColor);
  background: -ms-linear-gradient(top, $startColor, $endColor);
  background: -o-linear-gradient(top, $startColor, $endColor);
}

@mixin horizontal-gradient ($startColor: #eee, $endColor: white) {
  background-color: $startColor;
  background-image: -webkit-gradient(linear, left top, right top, from($startColor), to($endColor));
  background-image: -webkit-linear-gradient(left, $startColor, $endColor);
  background-image: -moz-linear-gradient(left, $startColor, $endColor);
  background-image: -ms-linear-gradient(left, $startColor, $endColor);
  background-image: -o-linear-gradient(left, $startColor, $endColor);
}

@mixin transform($string){
  -webkit-transform:  $string;
  -moz-transform: 	$string;
  -ms-transform: 	$string;
  -o-transform: 	$string;
  transform: 	$string;
}
@mixin scale ($factor) {
  -webkit-transform:  scale($factor);
  -moz-transform: 	scale($factor);
  -ms-transform: 	scale($factor);
  -o-transform: 	scale($factor);
  transform: 	scale($factor);
}
@mixin rotate ($deg) {
  -webkit-transform:  rotate($deg);
  -moz-transform: 	rotate($deg);
  -ms-transform:      rotate($deg);
  -o-transform: 	rotate($deg);
  transform: 	rotate($deg);
}
@mixin skew ($deg, $deg2) {
  -webkit-transform:  skew($deg, $deg2);
  -moz-transform: 	skew($deg, $deg2);
  -ms-transform:      skew($deg, $deg2);
  -o-transform: 	skew($deg, $deg2);
  transform: 	skew($deg, $deg2);
}
@mixin translate ($x, $y:0) {
  -webkit-transform:  translate($x, $y);
  -moz-transform: 	translate($x, $y);
  -ms-transform: 	translate($x, $y);
  -o-transform:       translate($x, $y);
  transform:       translate($x, $y);
}
@mixin translate3d ($x, $y: 0, $z: 0) {
  -webkit-transform:  translate3d($x, $y, $z);
  -moz-transform: 	translate3d($x, $y, $z);
  -ms-transform:      translate3d($x, $y, $z);
  -o-transform:       translate3d($x, $y, $z);
  transform:       translate3d($x, $y, $z);
}
@mixin perspective ($value: 1000) {
  -webkit-perspective: $value;
  -moz-perspective: 	 $value;
  -ms-perspective: 	 $value;
  perspective:         $value;
}
@mixin transform-origin ($x:center, $y:center) {
  -webkit-transform-origin: $x $y;
  -moz-transform-origin:    $x $y;
  -ms-transform-origin:     $x $y;
  -o-transform-origin:      $x $y;
  transform-origin:      $x $y;
}
