/*
* MAIN MODULE STYLES
*/

@import "../../sass/parts/variables-main";
@import "../../sass/parts/custom";
@import "../../../node_modules/bootstrap/scss/mixins";
@import "../../sass/parts/mixins";
@import "variables";


.page-header{
  position: fixed;
  top: 0rem;
  right: 0rem;
  left: 0rem;
  text-align: center;
  @include transition(2s, padding, cubic-bezier(.165,.84,.44,1));
  min-height: 5rem;
  padding: 0rem;
  z-index: 100;
  //background: $main_bg;

  @include display-flex();
  align-items: center;
  justify-content: center;

  #ph-logo-path{
    fill: $main_text_color;
  }

  &.page-header--homepage {
    background: transparent;

    #ph-logo-path {
      fill: $main_text_color;
    }
  }
}

.page-header--hide{
  @include translate(0%, -100%);
}

.page-header--homepage{
  padding: 4.125rem 0rem;
  //background: transparent;

  .page-header__logo {
    img, svg {
      height: 5.0625rem;
    }
  }

  #ph-logo-path{
    fill: $main_text_color;
  }

  .menu-button{
    opacity: 0;
  }
}

.page-header--color-def{
  background: rgba($main_bg, 0.5);

  #ph-logo-path{
    fill: $main_text_color;
  }

  .menu-button{
    span{
      background: $main_text_color;
    }
  }
}

@each $name, $color in $main_colors {
  .page-header--#{$name}{
    background: map_get($main_bgs, $name);//rgba(map_get($main_bgs, $name),0.5);

    #ph-logo-path{
      fill: $color;
    }

    .menu-button{
      span{
        background: $color;
      }
    }
  }
}

.page-header__logo{
  img,svg{
    display: inline-block;
    height: 1.5rem;
    @include transition(1s, all, cubic-bezier(.165,.84,.44,1));
  }

  #logo-path{
    fill: $main_text_color;
  }

  a{
    &:after{
      content: none;
    }
  }
}

.menu-button{
  position: absolute;
  top: 50%;
  right: $grid-gutter-width;
  @include translate(0%, -50%);
  @include transition(2s, opacity, cubic-bezier(.165,.84,.44,1));
  opacity: 1;

  width:40px;
  height:40px;
  margin: 1px 0px 0px;
  padding: 0px;
  cursor:pointer;
  background: none;
  z-index: 300;

  *{
    @include transition(.6s, width, cubic-bezier(.165,.84,.44,1));
  }

  span {
    display:block;
    background:$main_text_color;
  }

  .hamburger {
    position:absolute;
    height:100%;
    width:100%;
    @include center-absolute();
    span {
      width:32px;
      height:1px;
      position:absolute;
      top:3px;
      right:4px;
      margin:10px 0;
      &:nth-child(1){
        transition-delay:.5s;
      }
      &:nth-child(2){
        transition-delay:.625s;
        top:3px + 14px;
      }
    }
  }
  .cross {
    position:absolute;
    height:100%;
    width:100%;
    top: 0px;
    left: 0px;

    @include rotate(45deg);

    span{
      &:nth-child(1){
        height:0%;
        width:1px;
        position:absolute;
        top:10%;
        left:19px;
        transition-delay:0s;
        transition-property: height;
      }
      &:nth-child(2){
        width:0%;
        height:1px;
        position:absolute;
        left:10%;
        top:19px;
        transition-delay:.25s;
      }
    }
  }
  &.open {
    .hamburger {
      span {
        width:0%;
        &:nth-child(1){
          transition-delay:0s;
        }
        &:nth-child(2){
          transition-delay:.125s;
        }
        &:nth-child(3){
          transition-delay:.25s;
        }
      }
    }
    .cross {
      span {
        &:nth-child(1){
          height:80%;
          transition-delay:.625s;
        }
      }
      span {
        &:nth-child(2){
          width:80%;
          transition-delay:.375s;
        }
      }
    }
  }
}

@include media-breakpoint-up(xxl){
}
@include media-breakpoint-only(xl) {
}
@include media-breakpoint-only(lg) {
  .page-header--homepage {
    .page-header__logo {
      img, svg {
        height: 4.25rem;
      }
    }
  }
}
@include media-breakpoint-only(md) {
  .page-header--homepage {
    .page-header__logo {
      img, svg {
        height: 4.25rem;
      }
    }
  }
}
@include media-breakpoint-only(sm) {
  .page-header {
    min-height: 4.5rem;
  }

  .page-header--homepage {
    .page-header__logo {
      img, svg {
        height: 3.5rem;
      }
    }
  }

  .menu-button {
    right: $grid-gutter-width / 2;
    margin: 2px 0px 0px;

    .cross {
      span{
        &:nth-child(1){
          top:15%;
        }
        &:nth-child(2){
          left:15%;
        }
      }
    }
    &.open {
      .cross {
        span {
          &:nth-child(1){
            height:70%;
          }
        }
        span {
          &:nth-child(2){
            width: 70%;
          }
        }
      }
    }
  }
}
@include media-breakpoint-down(xs) {
  .page-header {
    min-height: 4rem;
  }
  .page-header--homepage {
    .page-header__logo {
      img, svg {
        height: 2rem;
      }
    }
  }
  .page-header__logo {
    img, svg {
      height: 1.23rem;
    }
  }

  .menu-button {
    right: $grid-gutter-width / 2;
    margin: 2px 0px 0px;

    .cross {
      span{
        &:nth-child(1){
          top:20%;
        }
        &:nth-child(2){
          left:20%;
        }
      }
    }
    &.open {
      .cross {
        span {
          &:nth-child(1){
            height:60%;
          }
        }
        span {
          &:nth-child(2){
            width: 60%;
          }
        }
      }
    }
  }
}
