/*
* MAIN MODULE STYLES
*/

@import "../../sass/parts/variables-main";
@import "../../sass/parts/custom";
@import "../../../node_modules/bootstrap/scss/mixins";
@import "../../sass/parts/mixins";
@import "variables";


.page-footer{
  padding: 5rem 0rem 4rem;

  .block {
    margin-bottom: 1rem;

    .head-block {
      @include font-size($main_font_size);
      line-height: $main_line_height;
      margin: 0 0 1rem;
    }
  }
}

.page-footer__logo{
  margin-bottom: 3rem;
}

.page-footer__menu{
  list-style: none;
  margin: 0 0rem 0rem 0rem;
  padding: 0;

  li{
    padding-bottom: 1rem;
    display: inline-block;
    width: 100%;
  }
}

@each $name, $color in $main_colors {
  .colorset--#{$name}{
    #f-logo-path{
      fill: $color;
    }

    &.page-footer{
      a{
        color: $color;

        &:after {
          background: $color;
        }

      }

      .block .head-block {
        color: rgba($color, 0.5);
      }

      .page-footer__menu{
        a{
          color: rgba($color, 0.5);

          &:hover, &:active, &:focus{
            color: $color;

            &:after {
              background: $color;
            }
          }

          &:after {
            background: rgba($color, 0.5);
          }
        }
      }
    }
  }
}


@include media-breakpoint-up(xxl){
  .page-footer__menu {
    //@include multiple-column(2);
  }
}
@include media-breakpoint-only(xl) {
  .page-footer__menu {
    //@include multiple-column(2);
  }
}
@include media-breakpoint-only(lg) {
}
@include media-breakpoint-only(md) {
}
@include media-breakpoint-only(sm) {
  .page-footer {
    .block {
      margin-bottom: 1.4rem;

      &.block-type-0{
        margin: 0px;

      }

      .head-block{
        margin: 0;
      }
    }
  }
}
@include media-breakpoint-down(xs) {
  .page-footer {
    .block {
      margin-bottom: 1.4rem;

      &.block-type-0{
        margin: 0px;

      }

      .head-block{
        margin: 0;
      }
    }
  }
}

@media (max-width: 480px) {
  .page-footer {
    @include font-size($main_font_size - 0.13);

    .block {
      .head-block {
        @include font-size($main_font_size - 0.13);
      }
    }
  }
}

@media (max-width: 380px) {
  .page-footer {
    @include font-size($main_font_size - 0.15);

    .block {
      .head-block {
        @include font-size($main_font_size - 0.15);
      }
    }
  }
}