// -- main variables
$main_font_family: 'basier_squareregular', sans-serif;
$main_font_size: 1rem;
$main_font_weight: 400;
$main_line_height: 1.4;

$main_colors: (
        'color-1': #13333A,
        'color-2': #C8A496,
        'color-3': #DAF0F0,
        'color-4': #8BACAD,
        'color-5': #C8A496,
        'color-6': #8BACAD,
);
$main_bgs: (
        'color-1': #ffffff,
        'color-2': #F2EEEA,
        'color-3': #8BACAD,
        'color-4': #13333A,
        'color-5': #f8f6f4,
        'color-6': #29464c,
);

$main_text_color: map_get($main_colors, 'color-1');
$main_bg: map_get($main_bgs, 'color-1');
$main_color: map_get($main_colors, 'color-2');
$main_border_color: #E5E5E5;
$main_border: 1px solid $main_border_color;

$headline_font_family: 'gangster_groteskregular', sans-serif;

$link_color: $main_text_color;
$link_text_decoration: none;
$link_hover_color: $main_text_color;
$link_hover_text_decoration: none;
$error_color: #F61237;