// =heads
//============================================================================*/

@mixin head_class() {
  font-family: $head_class_font_family;
  @include font-size($head_class_font_size);
  font-weight: $head_class_font_weight;
  margin: 0 0 1.3rem 0;
  line-height: $head_class_line_height - 0.3;

  a{
    &:after{
      content: none;
    }
  }
}
.head-class {
  @include head_class();
  color: $head_class_color;
}
.head-news-class {
  @include head_class();
  color: $head_class_color;
}

// =buttons
//============================================================================*/

@mixin main_button() {
  display: inline-block;
  padding: 5px 0px;
  color: $main_button_color;
  font-weight: $main_button_font_weight;
  @include font-size($main_button_font_size);
  position: relative;
  background: transparent;

  &:after{
    content: "";
    display: block;
    left: 0;
    right: 100%;
    height: 1px;
    bottom: 0;
    position: absolute;
    background: $main_button_color;
    @include transition(.6s, all, cubic-bezier(.165,.84,.44,1));
  }

  &:hover, &:focus, &:active {
    color: $main_button_color;

    &:after{
      right: 0%;
    }
  }
}
.button {
  @include main_button();
}

// =breadcrumbs
//============================================================================*/
#breadcrumbs {
  line-height: 31px;
  list-style: none;
  padding: 0;
  background: none;
  float: left;

  li {
    float: left;
    margin: 0 10px 0 0;

    a {
      color: $link_color;

      &:hover, &:focus, &:active {
        text-decoration: underline;
      }
    }

    &:before {
      content: '';
      margin: 0 10px 0 0;
    }
    &:first-child:before {
      display: none;
    }
  }
}

// =archive-box
//============================================================================*/

.archive-box {

  .archive-item {
    padding: 5px 0 5px 15px;
    border-left: $archive_box_border;
    margin: 0 0 0 25px;
  }
  .year {
    @include font-size($archive_box_year_font_size);
    margin: 10px 0 10px 0;

    &:first-child {
      margin: 0 0 10px 0;
    }
  }
}

// =news
//============================================================================*/
.news-block {
  margin: 0 0 20px 0;
  padding: 0 0 20px 0;
  border-bottom: $main_border;
}

// =upload-tmp-list
//============================================================================*/
#upload-tmp-list {

  ul {
    list-style: none;
    padding: 0;

    li {
      position: relative;
      border-bottom: $main_border;
      padding: 10px 0;
    }
  }
  p {
    max-width: 90%;
    display: inline-block;
  }
  i {
    position: absolute;
    top: 8px;
    right: 0;
    margin: 5px 0 0 0;
    display: inline-block;
    float: right;
    color: $error_color;
  }
}
.dropzone .dz-preview, .dropzone-previews .dz-preview {
  width: 130px;
  overflow: hidden;
}
.dz_filename {
  height: 80px;
  overflow: hidden;
}

// =search
//============================================================================*/
.search-result-list {

  .search-item {
    margin: 0 0 30px 0;
    padding: 0 0 30px 0;
    border-bottom: $main_border;

    .search-item-head {
      color: #000000;
      @include font-size(22);
      font-weight: 400;
      line-height: 1;
      text-align: left;
      margin: 0 0 30px 0;
    }
  }
}