$enable-responsive-font-sizes: true;

//$grid-columns:      12;
$grid-gutter-width: 28px;

$grid-breakpoints: (
        xxs: 0,
  // Extra small screen / phone
        xs: 340px,
  // Small screen / phone
        sm: 670px,//576
  // Medium screen / tablet
        md: 860px,//768
  // Large screen / desktop
        lg: 1080px,//992
  // Extra large screen / wide desktop
        xl: 1260px,//1200
  // XExtra large screen / wide desktop
        xxl: 1420px//1320
);

$container-max-widths: (
        xs: 334px,
        sm: 540px,
        md: 720px,
        lg: 960px,
        xl: 1140px,
        xxl: 1280px
);




